@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Roboto;
}

/* body {
  background-color: #77C4FA;
} */

.app {
  width: 100%;
  min-height: 100vh;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  overflow: hidden;
  transition: all 0.4s;
}

.app.cold {
  background-image: url('cold-bg.jpg');
}

.app.mid {
  background-image: url('mid-bg.jpg');
}

.app.warm {
  background-image: url('warm-bg.jpg');
}

.app::after {
  content: '';
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8);

}

main {
  width: 100%;
  height: 100%;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  background: rgba(0, 0, 0, 0.8)
}

.weather-box {
  color: white;
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  width: 280px;
  height: 200px;
  text-align: center;
  margin-top: 15px;
  font-size: 36px;
  margin-left: auto;
  margin-right: auto;
}

.date {
  font-size: 20px;
}

.status {
  font-size: 24px;
}

.location {
  margin-top: 10px;
}

.temp {
  padding: 20px 10px;
  border-radius: 1rem;
  height: 220px;
  font-size: 136px;
  font-weight: 100;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: -20px;
}

.min-max {
  font-size: 24px;
}

.max {
  position: relative;
  padding-bottom: 8px;
}

.min {
  padding-top: 8px;
}

.max::after {
  content: '';
  position: absolute;
  top: 100%;
  left: 0;

  height: 1px;
  background-color: rgba(255, 255, 255, 0.6);
  width: 100%;
}

.additional-info {
  margin: 87px auto 0 auto;
  width: 100%;
  max-width: 580px;
  margin-top: 105px;
  text-align: left;
}

.intro {
  color: white;
  font-weight: 100;
  font-size: 20px;
  position: relative;
  width: 100%;
  border-bottom: 25px;
  margin-left: auto;
  margin-right: auto;
}

.blocks {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  align-items: center;
  margin-top: 5px;
  margin-left: auto;
  margin-right: auto;
}

.info {
  width: 120px;
  height: 120px;
  margin-top: 10px;
  background-color: rgba(255, 255, 255, 0.15);
  border-radius: 5px;
  color: white;
  font-weight: 100;
  text-align: center;
  padding: 5px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.stat {
  margin-bottom: 5px;
}

.icon {
  font-size: 30px;
  margin-bottom: 5px;
}